import { createSlice } from "@reduxjs/toolkit";
const items = [];
//   localStorage.getItem("cartItems") !== null
//     ? JSON.parse(localStorage.getItem("cartItems"))
//     : [];

const initialState = {
  value: items,
};

export const cartItemsSlice = createSlice({
  name: "cartItems",
  initialState,
  reducers: {
    addItem: (state, action) => {
      const newItem = action.payload;

      const duplicate = state.value.filter(
        (e) =>
          e.slug !== newItem.slug &&
          e.color !== newItem.color &&
          e.size !== newItem.size
      );

      if (duplicate.length > 0) {
        state.value = state.value.filter(
          (e) =>
            e.slug !== newItem.slug ||
            e.color !== newItem.color ||
            e.size !== newItem.size
        );

        state.value = [
          ...state.value,
          {
            id: duplicate[0].id,
            slug: newItem.slug,
            color: newItem.color,
            size: newItem.size,
            price: newItem.price,
            quantity: newItem.quantity + duplicate[0].quantity,
          },
        ];
      } else {
        state.value = [
          ...state.value,
          {
            ...action.payload,
            id:
              state.value.length > 0
                ? state.value[state.value.length - 1] + 1
                : 1,
          },
        ];
      }
      console.log(state.value);
      //   localStorage.setItem(
      //     "cartItems",
      //     JSON.stringify(
      //       state.value.sort((a, b) => (a.id > b.id ? 1 : a.id < b.id ? -1 : 0))
      //     )
      //   );
    },
    updateItem: (state, action) => {
      const newItem = action.payload;

      const item = state.value.filter(
        (e) =>
          e.slug === newItem.slug &&
          e.color === newItem.color &&
          e.size === newItem.size
      );

      if (item.length > 0) {
        state.value = state.value.filter(
          (e) =>
            e.slug !== newItem.slug ||
            e.color !== newItem.color ||
            e.size !== newItem.size
        );

        state.value = [
          ...state.value,
          {
            ...newItem,
            id: item[0].id,
          },
        ];
      }
      // localStorage.setItem(
      //   "cartItems",
      //   JSON.stringify(
      //     state.value.sort((a, b) => (a.id > b.id ? 1 : a.id < b.id ? -1 : 0))
      //   )
      // );
    },

    removeItem: (state, action) => {
      const item = action.payload;

      state.value = state.value.filter(
        (e) =>
          e.slug !== item.slug || e.color !== item.color || e.size !== item.size
      );
    },
  },
});

export const { addItem, updateItem, removeItem } = cartItemsSlice.actions;
export default cartItemsSlice.reducer;
